export default {
  id: 'a-radio-group',
  name: '',
  eventTypes: [
    {
      id: 'change',
      name: '',
    },
  ],
  type: ['input', 'radio'],
  props: {
    value: {
      label: '',
      editors: 'string',
    },
    defaultValue: {
      label: '',
      editors: 'string',
    },
    options: {
      label: '',
      editors: {
        name: '',
        type: 'array',
        props: {
          keyName: 'value',
          fields: [
            {
              name: 'value',
              label: '',
            },
            {
              name: 'label',
              label: '',
            },
            {
              name: 'disabled',
              label: '',
              type: 'boolean',
            },
          ],
        },
      },
    },
    disabled: {
      label: '',
      editors: 'boolean',
    },
    size: {
      label: '',
      editors: 'select?@items=large|default|small',
      help: '',
    },
    buttonStyle: {
      label: '',
      editors: 'select?@items=outline|solid',
      help: '',
    },
    name: {
      label: '',
      editors: 'string',
    },
  },

  optionEditor: {
    title:'',
    tag: 'a-radio',
    getData(node) {
      return {
        value: node.getAttrValue('value'),
        label: node.getText(),
      };
    },
    setData(node, data) {
      node.setAttrValue('value', data.value);
      node.setText(data.label);
    },
  },

  vModel: ['value'],

  vucProxyOption: {
    textProxy: false,
  },
};
