export default {
  id: 'a-form',
  name: '',
  eventTypes: [],
  type: ['container', 'form'],
  slots: [
    {
      slot: 'default',
      name: '',
    },
  ],
  props: {
    model: {
      label: '',
      change(attr, old, node) {
        let inputs = node.querySelectorAll('.input');
        inputs.forEach(input => {
          let model = input.getDirective('model');
          if (model) {
            let modelValue = model.getAttrValue();
            let v = modelValue.split('.');
            if (v[0] == old.attrValue) {
              let v1 = [attr.attrValue].concat(v.slice(1)).join('.');
              input.setAttrValue(model.getAttrName(), v1);
            }
          }
        });
      },
    },
    rules: {
      label: '',
    },
    labelAlign: {
      label: '',
      editors: 'radio-group?@items=left:|right:',
    },
    layout: {
      label: '',
      editors: 'select?@items=horizontal:|vertical:|inline:',
    },
    labelCol: {
      label: '',
      editors: 'col',
    },
    wrapperCol: {
      label: '',
      editors: 'col',
    },
    hideRequiredMark: {
      label: '',
      editors: 'boolean',
    },
    colon: {
      label: '',
      editors: 'boolean?defaultValue=true',
    },
  },
};
