export const types = {
  string: '',
  number: '',
  url: 'url',
  email: 'email',
  integer: '',
  float: '',
  array: '',
  enum: '',
  boolean: '',

  object: '',
  method: '',
  regexp: '',
  date: '',
  hex: '16',
};

const formatterMap = {
  type(r) {
    return types[r.type];
  },

  required(r) {
    return '';
  },

  range(r) {
    return `(${r.min}-${r.max})`;
  },
  len(r) {
    return `(${r.len})`;
  },
  pattern(r) {
    return '';
  },
};

function isNumber(num) {
  return typeof num == 'number';
}

const isEnableMap = {
  type(r) {
    return r.type ? true : false;
  },

  required(r) {
    return r.required ? true : false;
  },

  range(r) {
    return isNumber(r.min) || isNumber(r.max);
  },
  len(r) {
    return isNumber(r.len);
  },
  pattern(r) {
    return r.pattern ? true : false;
  },
};

export function parseEnable(rule) {
  for (let k in isEnableMap) {
    let f = isEnableMap[k];
    rule.state = rule.state || {};
    rule.state[k] = f(rule);
  }
  return rule;
}

export function parseSubRules(rule) {
  let subRules = [];
  for (let k in rule.state) {
    let state = rule.state[k];
    if (state) {
      subRules.push(formatterMap[k](rule));
    }
  }
  return subRules;
}
