export default {
  id: 'a-checkbox-group',
  name: '',
  eventTypes: [
    {
      id: 'change',
      name: '',
    },
  ],
  slots: [
    {
      slot: 'label',
      name: '',
    },
  ],
  type: ['input', 'checkbox-group'],
  props: {
    value: {
      label: '',
    },
    options: {
      label: '',
      editors: {
        name: '',
        type: 'array',
        props: {
          keyName: 'value',
          fields: [
            {
              name: 'value',
              label: '',
            },
            {
              name: 'label',
              label: '',
            },
            {
              name: 'disabled',
              label: '',
              type: 'boolean',
            },
          ],
        },
      },
    },
    disabled: {
      label: '',
      editors: 'boolean',
    },
    name: {
      label: '',
      editors: 'string',
    },
  },
  vModel: ['value'],

  optionEditor: {
    title: '',
    tag: 'a-checkbox',
    getData(node) {
      return {
        value: node.getAttrValue('value'),
        label: node.getText(),
      };
    },
    setData(node, data) {
      node.setAttrValue('value', data.value);
      node.setText(data.label);
    },
  },

  vucProxyOption: {
    onBeforeRender(vucNode, context) {
      vucNode.dfs((node) => {
        context.cancelOutline(node);
      });
    },
  },
};
